import React , { useState } from "react";
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import NevigateButton from "./NevigateButton";
import SearchBar from "./SearchBar";
import axios from 'axios';
import { Link, TextField ,Paper, Typography } from "@material-ui/core";
import youtube from "../api/youtube";
import Chip from '@material-ui/core/Chip';
import SelectSearchBar from './SelectSearchBar';
import OptionsList from './OptionsList';

// function getTimes() {
//   return [0 , 12 , 90 , 100];
// }

export default ({ video }) => {
  if (!video) 
  return <Card><CardMedia
  image='../static/images/youtube_search.jpg'
  title="Contemplative Reptile"/></Card>
  
 
//https://upmostly.com/tutorials/using-axios-with-react-api-requests#:~:text=To%20use%20Axios%20with%20React%20we%20need%20to%20install%20Axios,json.&text=Open%20up%20App.
  const [handleTime, setHandleTime] = useState({
    currentTime: 0,
     index: 0,
    });

  const videoSrc = `https://www.youtube.com/embed/${video.id.videoId}?autoplay=1&start=${handleTime.currentTime}`;
  const videoServer =`https://www.youtube.com/watch?v=${video.id.videoId}`;
  const [times , setTimes] = useState([]);
  const [options , setOptions] = useState(null);

  function countUp(){
    setHandleTime({currentTime: times[handleTime.index], index: handleTime.index+1});
  } 

  function countDown() { 
    setHandleTime({currentTime: times[handleTime.index], index: handleTime.index-1});
  } 

//https://stackoverflow.com/questions/43871637/no-access-control-allow-origin-header-is-present-on-the-requested-resource-whe

function getWordOptions(searchTerm) {
  axios.post('https://final-project-get-it-2020.herokuapp.com/Ctrl_F_App/word/typos', {
    word : searchTerm,
    youtubeUrl : videoServer
  } )
  .then(res => {
    setOptions(res.data.typos);
  }).catch(error => {
    console.log(error)
});
if(options!=null){
//if(options.length<1){
  axios.post('https://final-project-get-it-2020.herokuapp.com/Ctrl_F_App', {
    word : searchTerm,
    youtubeUrl : videoServer
  } )
  .then(res => {
    setTimes(res.data.occurrences);
  }).catch(error => {
    console.log(error)
});
}
}

function getWordTimes(searchTerm) {
  axios.post('https://final-project-get-it-2020.herokuapp.com/Ctrl_F_App', {
    word : searchTerm,
    youtubeUrl : videoServer
  } )
  .then(res => {
    setTimes(res.data.occurrences);
  }).catch(error => {
    console.log(error)
});
}
  
  return (
    <React.Fragment>
      <Paper elevation={6} style={{ height: "50%" }}>
        <iframe
          frameBorder="0"
          height="100%"
          width="100%"
          title="Video Player"
          src={videoSrc}
        />
      </Paper>
      <Paper elevation={6} style={{ padding: "15px" }}>
        <Typography variant="h8" style={{ font:"menu" ,fontSize: "larger", fontWeight: "bold"}}>
          {video.snippet.title} - {video.snippet.channelTitle}
        </Typography>
        <Typography variant="subtitle1" style={{ font:"menu" ,fontSize: "medium"}}>
          {video.snippet.channelTitle}
        </Typography>
        <Typography variant="subtitle2" style={{ font:"menu" ,fontSize: "small"}}>
          {video.snippet.description}
          </Typography>
        <p></p>
        <Typography variant="subtitle3">
          {videoSrc}
          {handleTime.index}{times.length}
          </Typography>
      </Paper>
      <Paper>
      <SearchBar onSubmit={getWordOptions} title = {"Search on Video..."}/>
      <OptionsList options={options} onOptionSelect={getWordTimes} />
      {/* <SelectSearchBar onSubmit={getWordTimes} title = {"Search on Video..."} videoUrl = {videoServer}/>          */}
      {/* {options.length > 0 ? (
        <Paper>
          {options.map(option => (
              <Chip label = {option}
              onClick = {getWordTimes(label)}/>
          ))}
        </Paper>
      ): getWordTimes(searchTerm)} */}
      {handleTime.index == times.length ? (
        <Paper>
        <NevigateButton onBackSelect ={countDown} />
        </Paper>
      ) :
      handleTime.index < 0 ?(
        <Paper>
        <NevigateButton onNextSelect={countUp} />
        </Paper>
      ) :(
        <Paper>
        <NevigateButton onBackSelect ={countDown} onNextSelect={countUp} />
        </Paper>
      )}
      </Paper>

    </React.Fragment>
  );
}
