import React from 'react';
import {useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    '& > *': {
      margin: theme.spacing(1),
    },
  },
}));

  export default ({ onBackSelect, onNextSelect }) => {
    const classes = useStyles();
    const [count, setCount] = useState(0); 


    return (
        <div style = {{textAlign: 'center'}}>
          <ButtonGroup disableElevation variant="contained" color="secondary">
          <IconButton aria-label="back" className={classes.margin} size="small">
            <ArrowBackIcon fontSize="large" onClick={onBackSelect}/>
          </IconButton> 
          <IconButton aria-label="next" className={classes.margin} size="small">
            <ArrowForwardIcon fontSize="large" onClick={onNextSelect}/>
          </IconButton> 
          </ButtonGroup>      
        </div>

    );
  }

